  .carousel-container{
    position: relative;
    width: 70%;
  }  

  .capsule-carousel {
    width: 100%;
    height: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }

  .capsule:hover {
    -webkit-transform: scale(1.054);
        -ms-transform: scale(1.054);
            transform: scale(1.054);
    -webkit-filter: brightness(1.5);
            filter: brightness(1.5);
    -webkit-transition: -webkit-transform 0.6s ease, -webkit-filter 0.6s ease;
    transition: -webkit-transform 0.6s ease, -webkit-filter 0.6s ease;
    -o-transition: transform 0.6s ease, filter 0.6s ease;
    transition: transform 0.6s ease, filter 0.6s ease;
    transition: transform 0.6s ease, filter 0.6s ease, -webkit-transform 0.6s ease, -webkit-filter 0.6s ease;

    will-change: transform;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }

  .capsule:not(:hover) {
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    -webkit-filter: brightness(1);
            filter: brightness(1);
    -webkit-transition: -webkit-transform 0.3s ease, -webkit-filter 0.3s ease;
    transition: -webkit-transform 0.3s ease, -webkit-filter 0.3s ease;
    -o-transition: transform 0.3s ease, filter 0.3s ease;
    transition: transform 0.3s ease, filter 0.3s ease;
    transition: transform 0.3s ease, filter 0.3s ease, -webkit-transform 0.3s ease, -webkit-filter 0.3s ease;
  }

  a {
    text-decoration: none;
  }

  h2 {
    text-shadow: 2px 6px 6px rgba(0, 0, 0, 0.7);
    margin-bottom: 14px;
  }

  .text {
    margin-top: 0;
    margin-bottom: 2rem;
    font-size: 0.7em;

    text-shadow: 2px 6px 6px rgba(0, 0, 0, 0.7);
    font-family: 'Roboto Slab', serif;   
  }
  
  .slick-prev:before,
  .slick-next:before {
    font-size: 32px;
    color: rgba(255, 255, 255, 0.5);
  }

  .slick-prev {
    left: -64px;
  }
  
  .slick-next {
    right: -48px;
  }

  @font-face {
    font-family: 'Roboto Slab';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/static/RobotoSlab-Regular.ttf') format('truetype');
  }

  .carousel-mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    background: 
      -webkit-gradient(linear, right top, left top, from(rgba(0, 0, 0, 0.8)), 
      color-stop(5%, rgba(0, 0, 0, 0)), 
      color-stop(95%, rgba(0, 0, 0, 0)), 
      to(rgba(0, 0, 0, 0.8)));
    background: 
      -webkit-linear-gradient(right, rgba(0, 0, 0, 0.8) 0%, 
      rgba(0, 0, 0, 0) 5%, 
      rgba(0, 0, 0, 0) 95%, 
      rgba(0, 0, 0, 0.8) 100%);
    background: 
      -o-linear-gradient(right, rgba(0, 0, 0, 0.8) 0%, 
      rgba(0, 0, 0, 0) 5%, 
      rgba(0, 0, 0, 0) 95%, 
      rgba(0, 0, 0, 0.8) 100%);
    background: 
      linear-gradient(to left, rgba(0, 0, 0, 0.8) 0%, 
      rgba(0, 0, 0, 0) 5%, 
      rgba(0, 0, 0, 0) 95%, 
      rgba(0, 0, 0, 0.8) 100%);
  }


