.message-balloon {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    background-color: #f0f0f0;
    border: 2px solid #000000;
    padding: 10px;
    overflow: hidden;

    margin: 20px;
    margin-right: calc(15% - 2rem);
    margin-left: calc(15% - 2rem);

    border-radius: 10px;
    -webkit-box-shadow: 0px 12px 24px rgba(0, 0, 0, 0.5);
            box-shadow: 0px 12px 24px rgba(0, 0, 0, 0.5);

    visibility: hidden;
    opacity: 0;
  }
  
  .icon {
    margin-right: 0.5rem;
  }
  
  .icon img {
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 50%;
    -webkit-box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.7);
            box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.7);
  }
  
  .content {
    -ms-flex: 1 1;
        flex: 1 1;
    padding-right: 4rem;
  }
  
  .name {
    font-weight: bold;
    font-size: 0.5em;
    text-align: center;
    margin-top: -2.5px;
    color: black;
    font-family: 'Roboto Slab', serif;
  }
  
  .message .h {
    font-weight: bold !important;
    font-size: 1.3em !important;
    margin-bottom: 0.2em;
    text-shadow: none;
  }

  .message {
    font-size: 0.8em;
    color: black;
    text-shadow: 1px 5px 4px rgba(0, 0, 0, 0.5);
    font-family: 'Roboto Slab', serif;    
  }  

  @font-face {
    font-family: 'Roboto Slab';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/static/RobotoSlab-Regular.ttf') format('truetype');
  }

  @-webkit-keyframes pop-up {
    0% {
      -webkit-transform: scaleX(0) scaleY(0);
              transform: scaleX(0) scaleY(0);
      opacity: 0;
    }
    40% {
      -webkit-transform: scaleX(1.3);
              transform: scaleX(1.3);
    }
    70% {
      -webkit-transform: scaleX(0.8) scaleY(1.2);
              transform: scaleX(0.8) scaleY(1.2);
    }
    100% {
      -webkit-transform: scaleX(1) scaleY(1);
              transform: scaleX(1) scaleY(1);
      opacity: 1;
    }
  }

  @keyframes pop-up {
    0% {
      -webkit-transform: scaleX(0) scaleY(0);
              transform: scaleX(0) scaleY(0);
      opacity: 0;
    }
    40% {
      -webkit-transform: scaleX(1.3);
              transform: scaleX(1.3);
    }
    70% {
      -webkit-transform: scaleX(0.8) scaleY(1.2);
              transform: scaleX(0.8) scaleY(1.2);
    }
    100% {
      -webkit-transform: scaleX(1) scaleY(1);
              transform: scaleX(1) scaleY(1);
      opacity: 1;
    }
  }  

  .message-balloon.animate {
    visibility: visible;
    opacity: 1;

    -webkit-transform: scale(0);

        -ms-transform: scale(0);

            transform: scale(0);
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
    -webkit-animation-name: pop-up;
            animation-name: pop-up;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }


