.App {
  text-align: center;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;

  background: #0069a9;
}

.App-link {
  color: #61dafb;
}

.app-page {
  text-align: center;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;

  background: rgb(31,34,40);
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(31,34,40,1)), to(rgba(59,65,77,1)));
  background: -webkit-linear-gradient(bottom, rgba(31,34,40,1) 0%, rgba(59,65,77,1) 100%);
  background: -o-linear-gradient(bottom, rgba(31,34,40,1) 0%, rgba(59,65,77,1) 100%);
  background: linear-gradient(0deg, rgba(31,34,40,1) 0%, rgba(59,65,77,1) 100%);
  margin-left: 5vw;
  margin-right: 5vw;

  -webkit-box-shadow: 0px 0px 64px rgba(0, 0, 0, 0.8);

          box-shadow: 0px 0px 64px rgba(0, 0, 0, 0.8);
}


