.banner-image {
    width: 40vh;
    height: 40vh;
}

.banner {
    position: relative;
    width: 100%;
    background-color: black;
}

.social-icons {
    position: absolute;
    bottom: 0.5em;
    right: 1em;
}

.social-icons a {
    margin-left: 0.5em;
    color: whitesmoke;
    font-size: 1.5em;
}

.social-icons a:hover {
    color: #007bff;
}


