.music-case {
    position: relative;
}

.m-icon-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.m-icon {
    position: relative;
    font-size: 2em;
    padding-right: 0.2em;
    bottom: -0.1em;
}

.m-icon-wrapper a {
    color: rgb(214, 99, 4);
    font-size: 1em;

    text-shadow: 1px 4px 4px rgba(0, 0, 0, 0.5);
}

.m-icon-wrapper :hover {
    -webkit-filter: brightness(1.2);
            filter: brightness(1.2);
}

.music-player {
    height: 32px;
    width: 40%;
}

.music-name {
    font-size: 0.6em;
    margin: 0;
    color: rgb(194, 194, 194);

    font-family: Verdana, Geneva, Tahoma, sans-serif;
}


