  .capsule {
    height: 50vh;   

    width: 96% !important;
  }

  .capsule-content {
    position: relative;
    height: 90%;
    top: 3%;

    border: 4px solid #43a2e6;
    border-radius: 5px;
    background-color: #f9f9f9;
    -webkit-transition: opacity 0.3s ease;
    -o-transition: opacity 0.3s ease;
    transition: opacity 0.3s ease;

    background-color: black;
    -webkit-box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.8);
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.8);
  }

  .content-wrapper {
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;

    -ms-flex: 1 1;

        flex: 1 1;
  }

  .capsule-description {
    position: relative;
    width: 100%;

    overflow: hidden;
    background-color: black;
    opacity: 0.7;
  }

  .capsule-description .text{
    position: relative;
    font-size: 0.55em;
    padding: 6%;
    padding-top: min(6%, 16px);

    text-decoration: none;
    color: white;

    font-family: 'Roboto Slab', serif;  
    text-align: justify; 
    -webkit-filter: brightness(0.8); 
            filter: brightness(0.8);
  }

  .capsule img {
    position: relative;
    width: 100%;
    max-height: 100%;
    top: 0;
    height: auto;
  }

  .capsule video {
    position: absolute;
    height: 140%;
    max-width: 100%;

    left: 0; 
    right: 0; 
    margin-left: auto; 
    margin-right: auto;
  }

  .video-capsule {
    position: relative;
    height: 70%;
    width: 100%;
  }

  .video {
    -webkit-filter: brightness(0.7);
            filter: brightness(0.7);
  }

  .tag-wrap {
    position: absolute;
    bottom: -0.35em;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    overflow: hidden;

    -webkit-filter: brightness(0.7);

            filter: brightness(0.7);
  }

  .tag {
    position: relative;
    opacity: 1;
    border: 1px solid black;
    padding: 1px 12px 1px 12px;
    margin: 0;

    font-size: 0.45em;
    font-weight: bold;
    text-shadow: 2px 1px 6px rgba(0, 0, 0, 0.9);
    color: white;

    text-align: center;

    border-radius: 32px;
    -webkit-box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.5);
            box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.5);
  }

  .gold {
    background-color: #ff9c07;
  }

  .silver {
    background-color: #72a0b3;
  }

  .copper {
    background-color: brown;
  }

  @font-face {
    font-family: 'Roboto Slab';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/static/RobotoSlab-Regular.ttf') format('truetype');
  }


